import React, {useState, useEffect} from "react";
import Breadcrumb from "../Breadcrumb";
import { Link } from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import placeholderImg from "../../../assets/images/placeholderImg.svg";
import learnersProfileIco from "../../../assets/images/learnersProfileIco.svg";
// import { useLocation } from "react-router-dom";
import { apiGet, apiPost, axiosWithAuth } from "../../../utilities/axiosInstance";
import API_ENDPOINTS from "../../../utilities/apiEndPoints";
import { useAuth } from "../../../context/AuthContext";
import phoneIcon from "../../../assets/images/phone.png";


export default function LearnerList() {
  const {  userData, token } = useAuth();
  const [isLoading, setIsLoading]= useState(false);
  const [learnerList, setLearnerList]= useState([])
  const [filteredLearnersList, setFilteredLearnersList] = useState([]);

  const filterLearnerList=(e)=>{
    const searchTerm = e.toLowerCase();
    const filtered = learnerList.filter(item => {
      // Convert all values to strings and check if any field contains the search term
      return Object.values(item).some(value => 
        String(value).toLowerCase().includes(searchTerm)
      );
    });
    setFilteredLearnersList(filtered);
   // console.log(filtered)
  }

  useEffect(() => {
    
    const fetchLearnerStudent = async () => {
      setIsLoading(true)
      try {
      const authInstance = axiosWithAuth(token); // Pass the token to create the instance
      const responseNew = await authInstance.post(API_ENDPOINTS.YOUR_LEARNER_STUDENTS);
       const response = await responseNew.data
    // console.log(response)
        if (response.success && response.data) {
          setIsLoading(false)
          // console.log(response.data)
          setLearnerList(response.data)
          setFilteredLearnersList(response.data)
         
        } else {
          console.error("Unexpected response format:", response);
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      }
      finally{
        setIsLoading(false)
      }
    };

    fetchLearnerStudent();

   
  },[])



  return (
    <div>
      <div className="mainContent">
        <Breadcrumb />
        <div className="mainContent__assets">
          <Container>
            <Row>
              <Col lg={12}>
                {/* Account setting */}
                <div className="personalDetailsForm learnerList">
                  <h2>Your Learner Students</h2>

                  <div className="personalDetailsForm__wrp learnerList__wrp">
                    <div className="learnerList__search">
                      <input
                        type="text"
                        placeholder="Search by name, phone number "
                        className="form-control"
                         onChange={(e)=>filterLearnerList(e.target.value)}
                      />
                    </div>

                    {filteredLearnersList?.map((item,i)=>(
                      <div key={i} className="learnerList__card">
                      <div className="learnerList__cardLeft">
                        <div className="learnerList__profileCard">
                          <div className="learnerList__profileImg">
                            <img src={item?.profile_picture || placeholderImg} alt="" />
                          </div>
                          {item.myself == 1 ? 
                          <div className="learnerList__profileDetails">
                            <div className="learnerList__profileCate">
                                <img src={learnersProfileIco} alt="" />
                              Learner
                            </div>
                            <h3>{item?.first_name} {item?.last_name}</h3>
                            <span>Age {item?.age}</span>
                          </div>
 : 
 <>
                           <div className="learnerList__profileDetails">
                            <div className="learnerList__profileCate">
                                <img src={learnersProfileIco} alt="" />
                              Learner
                            </div>
                            <h3>{item?.learner_first_name} {item?.learner_last_name}</h3>
                            <span>Age {item?.age}</span>
                          </div>

                          <div className="learnerList__profileDetails">
                            <div className="learnerList__profileCate">
                                <img src={learnersProfileIco} alt="" />
                               {item?.relation_name}
                            </div>
                            <h3>{item?.first_name} {item?.last_name}</h3>
                            <a href={`tel:${item?.phone_no}`}> <img src={phoneIcon} alt="" /> {item?.phone_no}</a>
                            
                          </div>
                          </>
}
                        </div>
                      </div>
                      <div className="learnerList__cardRight">
                        <div className="learnerList__cardButtons">
                          <Link to="/instructor/NewBooking" state={{ id: item?.id, address:item?.address }} className="btn btn-teal">
                            PROPOSE BOOKING
                          </Link>
                          <Link to={`/instructor/learner-list-details/${item?.id}`} className="btn btn-grey">
                            LEARNER PROFILE
                          </Link>
                        </div>
                      </div>
                    </div>
                    ))}
                    
                  
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </div>
  );
}
